import { Component, OnInit } from '@angular/core';
import {TranslateService} from 'ng2-translate';
import { Injectable} from '@angular/core';
import { Observable } from 'rxjs/Observable';
import {Http} from '@angular/http';
// import 'rxjs/Rx';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';
  navigationMenuOpen = false;
  country = 'UA';
  data;
  logo = 'ukr';

  constructor(public translate: TranslateService, private http: Http) {
    this.translate.addLangs(['en', 'rus', 'ukr']);
    this.translate.setDefaultLang('ukr');

    const browserLang = this.translate.getBrowserLang();
    this.translate.use(browserLang.match(/en|rus|ukr/) ? browserLang : 'ukr');
  }
  ngOnInit() {
    const userLang = navigator.language;
    console.log(userLang);
    

    if (localStorage.getItem('language') != null) {
        const lang = localStorage.getItem('language');
        this.translate.use(lang);
    } else if (localStorage.getItem('language') === null) {
      if (userLang.match('en')) {
          this.translate.use('en');
          
      } else if (userLang.match('rus')) {
          this.translate.use('rus');
 
      } else if (userLang.match('ukr')) {
          this.translate.use('ukr');
     
      }
    }
  }
  setLang(language) {
    localStorage.setItem('language', language);
    this.translate.use(language);
    this.logo = language;
  }
  handleClickMenu() {
    this.navigationMenuOpen = !this.navigationMenuOpen;
    console.log('changed');
  }
}



