import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css']
})
export class AboutComponent implements OnInit {
  activeSlide;
  slidesOnPage;
  modal = 0;
  slide_of_employees;
  constructor() {
  }

  ngOnInit() {
    this.activeSlide = 0;
    this.slide_of_employees = 0;
  }
  nextEmployees() {
    // for three employees
    // if (this.slide_of_employees === 0) {
    //   this.slide_of_employees ++;
    // } else if (this.slide_of_employees === 1) {
    //   this.slide_of_employees ++;
    // } else {
    //   this.slide_of_employees = 0;
    // }

    //test
    if (this.slide_of_employees === 0) {
      this.slide_of_employees ++;
    } else {
      this.slide_of_employees = 0;
    }
  }
  previousEmployees() {
    // for three employees
    // if (this.slide_of_employees === 2) {
    //   this.slide_of_employees --;
    // } else if (this.slide_of_employees === 1) {
    //   this.slide_of_employees --;
    // } else {
    //   this.slide_of_employees = 2;
    // }

    //test
    if (this.slide_of_employees === 1) {
      this.slide_of_employees --;
    } else {
      this.slide_of_employees = 1;
    }
  }
  nextSlide() {
    if (this.activeSlide !== 1) {
      this.activeSlide = this.activeSlide + 1;
    } else if (this.activeSlide === 1) {
      this.activeSlide = this.activeSlide - 1;
    }
  }
  prevSlide() {
    if (this.activeSlide !== 0) {
      this.activeSlide = this.activeSlide - 1;
    } else if (this.activeSlide === 0) {
      this.activeSlide = this.activeSlide + 1;
    }
  }
}



