import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {
  activeSlide;
  slidesOnPage;

  constructor() {
  }

  ngOnInit() {
    this.activeSlide = 0;
  }

  nextSlide() {
    console.log(this.slidesOnPage);
    console.log('next slide');
    if (this.activeSlide !== 2) {
      this.activeSlide = this.activeSlide + 1;
    }
  }

  prevSlide() {
    console.log('prev slide');
    const oldSlide = this.activeSlide;
    if (this.activeSlide !== 0) {
      this.activeSlide = this.activeSlide - 1;
    }
  }

  getActiveSlide() {
    return '0' + (this.activeSlide + 1);
  }

  getNextSlide() {
    let result = '';
    if (this.activeSlide !== 2) {
      result = '0' + (this.activeSlide === 2 ? 3 : this.activeSlide + 2);
    }
    return result;
  }
  getPrevSlide() {
    let result = '';
    if (this.activeSlide !== 0) {
      result = '0' + (this.activeSlide);
    }
    return result;
  }
}



